import React, { Component } from 'react'

export default class Nav extends Component {

    render() {
        let header_style = {
            width:"100%",
            height:'50px',
            backgroundColor:'#272727'
            
        }
        return (
            <div style = {header_style}>
                <img src='/images/logo.png'/>
                <img/>
            </div>
        )
    }
}
